html {
  font-size: 62.5%; /* 10px = 1 REM */
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Atkinson Hyperlegible Next", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img,
picture {
  width: 100%;
  display: block;
  height: auto;
}

h1,
h2,
h3,
h4 {
  font-family: "Atkinson Hyperlegible Next", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.barra-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 1024px) {
  .barra-navegacion {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .slogan {
    display: block;
    text-align: right;
    padding-right: 10%;
    grid-column-start: 1;
    grid-column-start: 2;
    line-height: 0;
  }
}
.barra-navegacion .logo img {
  width: 30rem;
}

@media (min-width: 1024px) {
  .barra-navegacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .barra-navegacion .logo {
    flex: 1;
    padding-left: 5%;
  }
  .barra-navegacion .contenedor-menu {
    flex: 2;
  }
  .barra-navegacion .logo img {
    width: 30rem;
  }
}
.menu-principal {
  /* display: none; */
}

.menu-principal .menu a {
  color: #6a3357;
  font-size: 1.8rem;
  font-weight: 600;
  display: block;
  text-align: center;
  padding: 0.5rem 2rem;
}

.slogan {
  color: #6a3357;
  font-size: 2rem;
  font-weight: 600;
}

@media (min-width: 1024px) {
  .menu-principal {
    display: block;
  }
  .menu-principal .current_page_item > a {
    color: #96989b;
  }
  .menu-principal .menu {
    display: flex;
    justify-content: flex-end;
    align-items: end;
    gap: 2rem;
  }
  .menu-principal .menu a {
    padding: 0;
  }
  .contenedor-menu {
    display: grid;
    grid-template-columns: 10fr 1fr;
  }
  .contenedor-menu .menu-principal,
  .contenedor-menu .menu-social {
    border-bottom: 3px solid #96989b;
    padding-top: 0;
  }
  .contenedor-menu .slogan {
    grid-column-start: 1;
    grid-column-end: 2;
  }
}
.menu-social {
  display: flex;
  justify-content: center;
  padding-top: 2rem;
}